<template>
  <div  id="withdraw">
  <div class="tit">支付通道</div>
	<div class="w_box">
		 <div v-if="list.length != 0" v-for="(item,index) in list"  :key="item.index">
		 	<img :src="item.img">
		 	<div v-if="item.type == 'aliwap'">支付宝H5支付</div>
		 	<div v-if="item.type == 'alipay'">支付宝扫码支付</div>
		 	<div v-if="item.type == 'wechat'">微信扫码支付</div>
		 	<div>渠道费率 <span>{{item.service}}</span></div>
		 	<div><span></span><span>启用中</span></div>
		 	<div @click="tops()">关闭</div>
		 </div>
	</div>
  </div>
</template>
<script>
    export default {
      data(){
      	return {
           list:[]
         }
      },
      mounted(){
		    this.getpayment();
      },
      methods: {
        getpayment(){
          if(localStorage.getItem('payment')){
            var payment = JSON.parse(localStorage.getItem('payment'));
            if(payment.time < new Date().getTime()){
              this.ajaxlist();
            }else{
              this.list = payment.data.list;
            }
          }else{
            this.ajaxlist();
          }

        },
        ajaxlist(){
          this.$http.get('api/user/payment',{}, res => {
               if (res.status == 200) {
                   if(res.data.code == 200){
                     var local = {};
                         local.time = new Date().getTime() + 100000;
                         local.data = res.data.data;
                     localStorage.setItem('payment',JSON.stringify(local));
                      this.list = res.data.data.list;
                   }else if(res.data.code == 201){
                     this.$Message.warning(res.data.msg);
                   }
               } else {
                  console.log(res.message);
               }
          });
        },
        tops(){
          this.$Message.warning("关闭通道请咨询客服");
        }
      }
    }
</script>



